/* Header */
.codrops-header {
  margin: 0 auto;
  padding: 4em 1em;
  text-align: center;
  color: #f8fdff;
}

.codrops-header h1 {
  margin: 0;
  font-weight: 800;
  font-size: 4em;
  line-height: 1.3;
}

.codrops-header h1 span {
  display: block;
  padding: 0 0 0.6em 0.1em;
  font-size: 20%;
  font-weight: 300;
  color: #eafaff;
}

/* To Navigation Style */
.codrops-top {
  width: 100%;
  text-transform: uppercase;
  font-size: 0.69em;
  line-height: 2.2;
}

.codrops-top a {
  display: inline-block;
  padding: 0 1em;
  text-decoration: none;
  letter-spacing: 1px;
}

.codrops-top span.right {
  float: right;
}

.codrops-top span.right a {
  display: block;
  float: left;
}

.codrops-icon:before {
  margin: 0 4px;
  text-transform: none;
  font-weight: normal;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  font-family: "codropsicons";
  line-height: 1;
  -webkit-font-smoothing: antialiased;
}

.codrops-icon-drop:before {
  content: "\E001";
}

.codrops-icon-prev:before {
  content: "\E004";
}

body {
  margin: 0;
  padding: 0;
  background: white;
  color: #555;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.grid-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(6, minmax(300px, 600px));
  grid-gap: 7rem;
  grid-auto-flow: raw;
  margin: 80px 70px;
}

.grid-item {
  display: flex;
  position: relative;
  overflow-y: scroll;
  overflow-x: hidden;
}

.wrapper {
  position: relative;
  text-align: center;
}

.header-wrapper {
  background: rgb(128, 0, 42);
  margin-bottom: 50px;
}

.button-div {
  background: white;
  position: absolute;
  top: 0px;
  right: 0px;
  text-align: center;
}

.button-wrapper {
  position: absolute;
  top: 160px;
  right: 10px;
}
.table-wrapper {
  position: absolute;
  left: 0;
  width: 440px;
}

.alert {
  width: 440px;
  height: 50px;
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
}
.check.valid {
  color: green;
  background-color: white;
  margin: 2.9px 0px;
  font-size: 21.5px;
  border: black;
  padding: 5.5px 10px;
  cursor: pointer;
  outline-style: none;
}

.empyt-error {
  width: 440px;
  height: 50px;
  background-color: white;
}

.check.warning {
  color: rgb(245, 212, 23);
  background-color: white;
  margin: 1px 0px;
  font-size: 24px;
  border: black;
  padding: 5.5px 10px;
  cursor: pointer;
  outline-style: none;
}

.check.error {
  color: rgb(228, 8, 8);
  background-color: white;
  margin: 1px 0px;
  font-size: 24px;
  border: black;
  padding: 5.5px 10px;
  cursor: pointer;
  outline-style: none;
}

.close {
  width: 32px;
  height: 32px;
  opacity: 0.1;
}

.close:hover {
  opacity: 1;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

